<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <div class="btn_box">
        <Button type="primary" @click="downloadExcel">导出列表</Button>
      </div>
      <Table :columns="columns" :data="tableData">
          <template slot-scope="{ row }" slot="1">
          <span style="color:#538FFF">{{row['1']}}</span>
        </template>
        <template slot-scope="{ row }" slot="2">
          <span style="color:#538FFF">{{row['2']}}</span>
        </template>
        <template slot-scope="{row}" slot="action">
          <div>
            <button @click="handleLook(row)">查看撤案申请书</button>
            <button @click="handleBack(row)">同意撤回</button>
            <button @click="repeat(row)">重新生成文书</button>
          </div>
        </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="currentPage"
        :total="total"
        :page-size="pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
    <!-- 同意撤回 -->
     <Modal v-model="sureDialog" :footer-hide="true" title="操作">
      <Form ref="formValidate" :model="form" :rules="ruleValidate" :label-width="110">
        <FormItem label="文本类型" prop="type">
          <RadioGroup v-model="form.type">
            <Radio label="1">撤案决定书（组庭前）</Radio>
            <Radio label="2">撤案决定书（组庭后）</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="文本来源" prop="source">
          <RadioGroup v-model="form.source">
            <Radio label="1">结案模板</Radio>
            <Radio label="2">文件上传</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="选择文书模板" prop="template" v-if="form.source==1|| form.source==''">
          <Select v-model="form.template" placeholder="请选择">
            <Option value="beijing">New York</Option>
            <Option value="shanghai">London</Option>
            <Option value="shenzhen">Sydney</Option>
          </Select>
        </FormItem>
        <FormItem label="上传问题清单" prop="template" v-if="form.source==2">
          <div style="display:flex">
           <Upload 
            action="//jsonplaceholder.typicode.com/posts/"
             :before-upload="handleUpload">
                <Button type="primary" size="small">浏览...</Button>
            </Upload>
            <div style="margin-left:10px;height: 40px;">{{form.file? form.file.name: '未选择文件'}}</div>
          </div>
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="sureDialog=false">取消</Button>
        <Button type="primary" @click="submit">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue";
const columns = [
  { title: "案件编号", slot: "1" },
  { title: "案号" ,slot: "2"},
  { title: "申请人" },
  { title: "被申请人" },
  { title: "案件类型" },
  { title: "争议金额(元)" },
  { title: "仲裁费(元)" },
  { title: "案件状态" },
  { title: "操作", align: "center", width: 300, slot: "action" }
];
export default {
  components: {
    listSearch
  },
  data() {
    return {
      searchData: {
          number:"",
          appeal:"",
          backAppeal:"",
          regName:[],
          caseType:[]
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "appeal",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "backAppeal",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "regName",
          label: "注册名称",
          placeholder: "请选择注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "caseType",
          label: "案件类型",
          placeholder: "请选择案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        }
      ],
      currentPage:1,
      total:0,
      pageSize:10,
      columns:columns,
      tableData:[{1:1,2:2}],
      sureDialog:false,
      form: {
        type: "",
        source: "",
        template: "",
        file:""
      },
      ruleValidate: {
        type: [{ required: true, message: "请选择类型", trigger: "change" }],
        source: [{ required: true, message: "请选择类型", trigger: "change" }],
        template: [{ required: true, message: "请选择模板", trigger: "change" }]
      },
      
    };
  },
  methods:{
      goSearch(){},
      downloadExcel(){
          this.download()
      },
      // 查看
      handleLook(row){
          
      },
      // 同意撤回
      handleBack(row){

      },
      // 重新生成文书
      repeat(row){
        this.sureDialog=true;
        this.$nextTick(()=>{
          this.$refs["formValidate"].resetFields();
        })
      },
      handleUpload (file) {
      console.log(file)
        this.form.file = file;
        // console.log(this.form)
        return false;
    },
      submit(){
        this.$refs["formValidate"].validate(valid => {
        console.log(this.form);
        if (valid) {
          // 模板 1 模板2 文件
            this.form.source==1 ? this.form.file='':this.form.template=''
          this.$Message.success("Success!");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
      },
        //处理文件流
      download(data) {
        if (!data) {
          return
        }
        let url = window.URL.createObjectURL(new Blob([data]));
        let link = document.createElement('a');
        link.style.display = 'none';
        link.href = url;
        link.setAttribute('download', '留资详情.xls');
        document.body.appendChild(link);
        link.click()
      },

  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
.wrap {
  .table_box {
    background-color: #fff;
    margin-top: 30px;
    padding: 20px;
    .btn_box {
      text-align: right;
      margin-bottom: 20px;
    }
  }
}
</style>
